


















import Vue from 'vue'
import { isValidURL } from '@/utils/url'

export default Vue.extend({
  name: 'Preview',
  props: {
    loading: {
      type: Boolean,
      default: true,
    },
    link: {
      type: String,
    },
    label: {
      type: String,
    },
    photo: {
      type: String,
    },
    listview: {
      type: Boolean,
      default: false,
    },
  },
  components: {
    'feather-camera': () =>
      import('vue-feather-icons/icons/CameraOffIcon' /* webpackChunkName: "icons" */),
    'feather-loader': () =>
      import('vue-feather-icons/icons/LoaderIcon' /* webpackChunkName: "icons" */),
  },
  computed: {
    classes(): Record<string, boolean> {
      return {
        'plant-preview': true,
        loading: this.loading,
        'no-photo': this.photo === null || !this.isPhoto,
        listview: this.listview,
      }
    },
    isPhoto(): boolean {
      return this.photo && isValidURL(this.photo)
    },
  },
})
